import Image from "next/image";
import { useRouter } from "next/navigation";

export default async function ItemDetailsPage({ params }) {
    const { id } = params;
    const router = useRouter();

    const res = await fetch(`https://server-sage-gamma.vercel.app/api/products/${id}`);
    const product = await res.json();

    return (
        <div className="px-4 md:px-20 py-12 bg-green-50 min-h-screen">
            <button
                className="mb-4 text-green-600 hover:underline"
                onClick={() => router.back()}
            >
                ‚Üê Back
            </button>

            <div className="bg-white rounded-lg shadow overflow-hidden">
                <div className="relative w-full h-64 md:h-96">
                    <Image
                        src={product.image || "/items/default.jpg"}
                        alt={product.title}
                        fill
                        className="object-cover"
                    />
                </div>
                <div className="p-6">
                    <h1 className="text-3xl font-bold mb-2">{product.title}</h1>
                    <p className="text-green-700 mb-4">{product.shortDescription}</p>
                    <p className="text-green-900 font-semibold mb-2">Price: ${product.price}</p>
                    {product.priority && <p className="text-green-800 mb-2">Priority: {product.priority}</p>}
                    {product.date && <p className="text-green-800 mb-2">Date: {product.date}</p>}
                    <p className="text-gray-700">{product.description}</p>
                </div>
            </div>
        </div>
    );
}
